body{
  background-color:#9266ff;
}
.head{
  margin-top:10px;
  text-align:center;
  font-size:50px;
  color:#3f3160;
}
.App{
  background-color:#3f3160;
  min-height:500px;
  width:400px;
  margin:40px auto;
}
#to-do-form input{
  background-color: #1b7089;
  border:0;
  width:200px;
  height:50px;
  padding:0 20px;
  margin:20px;
  font-size:18px;
  border-radius:10px;
  color:#fff;
}
#to-do-form input::placeholder{
  color:rgba(255,255,255,0.5);
}
#to-do-form input:focus,#to-do-form button:focus {
  outline:none;
}
#to-do-form button{
  height:50px;
  width:80px;
  border-radius:5px;
  border:0;
  background-color:rgb(207,156,24);
  cursor:pointer;
  font-weight:bolder;
  color:#fff;
}